//引入axios
import axios from 'axios';

axios.defaults.baseURL = 'https://novel.syc.im/v1/';
// axios.defaults.baseURL = 'http://127.0.0.1:9292/v1/';
//设置默认请求头
axios.defaults.headers.post['Content-Type'] = 'application/json';
axios.defaults.timeout = 10000;

export default {
	//get请求
	get(url, param) {
		return new Promise((resolve, reject) => {
			axios({
				method: 'get',
				url,
				params: param
			}).then(res => {
				resolve(res.data)
			}).catch(err => {
				reject(err)
			});
		})
	},
	//post请求
	post(url, param) {
		return new Promise((resolve, reject) => {
			axios({
				method: 'post',
				url,
				data: param
			}).then(res => {
				resolve(res.data)
			}).catch(err => {
				reject(err)
			});
		})
	}
}