<template>
    <div>
        <s-header :title="title" path="/" isSort :sort.sync="sort"></s-header>
        <s-main>
            <div class="s-items" v-for="(item, index) in list" :key="index">
                <div class="s-item" @click='gotoDetail(item.pid)'>
                    <span class="s-name">{{ item.name }}</span>
                </div>
            </div>
        </s-main>
    </div>
</template>

<script>
    export default {
        name: "List",
        data() {
            return {
                id: this.$route.query.id,
                title: this.$route.query.title || '章节列表',
                list: [],
                sort: 'desc'
            }
        },
        watch: {
            sort: {
                handler(val) {
                    this.getList();
                    return val;
                },
                deep: true
            }
        },
        mounted() {
            document.title = this.title;
            this.getList();
        },
        methods: {
            // 跳转小说详情
            gotoDetail: function (id) {
                this.$router.push({
                    path: '/detail',
                    query: {
                        bid: this.id,
                        pid: id
                    }
                });
            },
            // 获取章节列表
            getList: function () {
                let params = {
                    bid: this.id,
                    sort: this.sort
                };
                this.$api.chapter(params).then(res => {
                    if (res.code === 0) {
                        this.list = res.data;
                        if (res.data[0].id === 1) {
                            localStorage.setItem('minPid', res.data[0].pid);
                        } else {
                            localStorage.setItem('maxPid', res.data[0].pid);
                        }
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .s-items {
        margin: 0;
    }

    .s-item {
        background: #fff;
        padding: .1rem;
        overflow: hidden;
        border-bottom: .01rem #dfdfdf solid;
    }

    .s-name {
        width: calc(100vw - .2rem);
        font-size: .16rem;
        color: #333;
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
</style>