<template>
    <div>
        <s-header title="小说列表"></s-header>
        <s-main>
            <div class="s-items">
                <div v-for="(item,index) in list" class="s-item" :key="index" @click='gotoList(item.bid, item.title)'>
                    <!--<div class="s-img">-->
                        <!--<img :src="item.img"/>-->
                    <!--</div>-->
                    <div class="s-bd-r">
                        <p class="s-title">{{ item.title }}</p>
                        <p class="s-author">{{ item.author }}</p>
                        <p class="s-update" v-if="item.update">更新：{{ item.update }}</p>
                        <p class="s-chapter" v-if="item.chapter">最新：{{ item.chapter }}</p>
                    </div>
                </div>
            </div>
        </s-main>
    </div>
</template>

<script>
    export default {
        name: "Index",
        data() {
            return {
                list: []
            }
        },
        mounted() {
            document.title = '小说列表';
            this.getList();
        },
        methods: {
            // 跳转章节列表
            gotoList: function (id, title) {
                this.$router.push({
                    path: '/list',
                    query: {
                        id: id,
                        title: title
                    }
                });
            },
            // 获取小说列表
            getList: function () {
                this.$api.books().then(res => {
                    if (res.code === 0) {
                        this.list = res.data;
                    }
                });
            }
        }
    }
</script>

<style lang="less" scoped>
    .s-items {
        margin: .1rem;
    }

    .s-item {
        height: .9rem;
        background: #fff;
        display: flex;
        align-items: center;
        padding: .1rem;
        margin-bottom: .1rem;
        overflow: hidden;
        border-radius: .04rem;
    }

    .s-img {
        width: .8rem;
        background: #f2f1e3;
        overflow: hidden;

        img {
            width: .8rem;
            height: .8rem;
            display: flex;
            align-items: center;
        }
    }

    .s-bd-r {
        flex: 1;
        padding-left: .1rem;
    }

    .s-title {
        font-size: .16rem;
        display: block;
        color: #333;
    }

    .s-author {
        font-size: .14rem;
        display: block;
        color: #666;
    }

    .s-update {
        font-size: .14rem;
        display: block;
        color: #999;
    }

    .s-chapter {
        font-size: .14rem;
        display: block;
        color: #999;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
</style>