import Vue from 'vue';
import Router from 'vue-router';
import Index from './views/Index.vue';
import List from './views/List.vue';
import Detail from './views/Detail.vue';

Vue.use(Router);

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'Index',
            component: Index
        },
        {
            path: '/list',
            name: 'List',
            component: List
        },
        {
            path: '/detail',
            name: 'Detail',
            component: Detail
        }
    ]
});
