import req from '../api/requestType'

// 小说列表
export const books = param => {
	return req.get('book/list', param);
};

// 章节列表
export const chapter = param => {
	return req.get('book/chapter/list', param);
};

// 小说详情
export const detail = param => {
	return req.get('book/detail', param);
};