<template>
    <header class="s-header">
        <i class="s-icon s-left" @click="back" v-show="path !== ''"></i>
        <span class="s-header__title">{{ title }}</span>
        <i class="s-icon s-sort" @click="sort" v-show="isSort"></i>
    </header>
</template>

<script>
    export default {
        name: "Header",
        props: {
            title: {
                type: String,
                default: ''
            },
            path: {
                type: String,
                default: ''
            },
            isSort: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                status: true,
                isWeixin: window.isWeixin
            }
        },
        methods: {
            // 返回上层
            back() {
                this.$router.replace(this.path);
            },
            // 排序
            sort() {
                this.status = !this.status;
                this.$emit('update:sort', this.status ? 'desc' : 'asc');
            }
        }
    }
</script>

<style scoped lang="less">
    .s-header {
        height: .5rem;
        background: #d1cfa9;
        display: flex;
        align-items: center;
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        z-index: 99;
        padding: 0 .10rem;

        .s-left {
            font-size: .18rem;
            color: #fff;
        }

        &__title {
            flex: 1;
            text-align: center;
            color: #fff;
            font-size: .14rem;
        }

        .s-sort {
            font-size: .18rem;
            color: #fff;
        }
    }
</style>