<template>
    <div>
        <s-header :title="title" :path="'/list?id=' + bid"></s-header>
        <s-main>
            <div class="s-detail" v-show="content" v-html="content" @click="setting"></div>
        </s-main>
        <div class="s-setting" v-show="show">
            <span @click="prev">上一章</span>
            <span @click="gotoList">目录</span>
            <span @click="next">下一章</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Detail",
        data() {
            return {
                bid: this.$route.query.bid,
                id: this.$route.query.pid,
                content: '',
                show: false,
                title: ''
            }
        },
        mounted() {
            this.getDetail(this.id);
        },
        methods: {
            // 获取详情
            getDetail: function (id) {
                let params = {
                    bid: this.bid,
                    pid: id
                };
                this.$api.detail(params).then(res => {
                    if (res.code === 0) {
                        document.title = res.data.name;
                        this.title = res.data.name;
                        this.content = res.data.content.replace(/\s+/g, '<p>');
                    }
                });
            },
            // 弹出设置
            setting: function () {
                this.show = !this.show;
            },
            // 返回目录
            gotoList: function () {
                this.$router.replace({
                    path: '/list',
                    query: {
                        id: this.bid
                    }
                });
            },
            // 上一章
            prev: function () {
                let pid = localStorage.getItem('minPid');
                if (Number(this.id) <= Number(pid)) {
                    return
                }
                this.id = Number(this.id) - 1;
                this.getDetail(this.id);
                window.scrollTo(0, 0);
                this.$router.replace({
                    path: '/detail',
                    query: {
                        bid: this.bid,
                        pid: this.id
                    }
                });
            },
            // 下一章
            next: function () {
                let pid = localStorage.getItem('maxPid');
                if (Number(this.id) >= Number(pid)) {
                    return
                }
                this.id = Number(this.id) + 1;
                this.getDetail(this.id);
                window.scrollTo(0, 0);
                this.$router.replace({
                    path: '/detail',
                    query: {
                        bid: this.bid,
                        pid: this.id
                    }
                });
            }
        }
    }
</script>

<style lang="less" scoped>
    .s-detail {
        padding: .1rem;
        font-size: .18rem;
        color: #333;
        line-height: 180%;

        /deep/ p {
            padding: .1rem 0;
        }
    }

    .s-setting {
        height: .5rem;
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        background: #d1cfa9;
        z-index: 99;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 .1rem;
    }
</style>